.no-js .morph-button .morph-content,
.no-js .morph-button .morph-content > div {
  position: relative;
  width: auto;
  height: auto;
  opacity: 1;
  visibility: visible;
  top: auto;
  left: auto;
  -webkit-transform: none;
  transform: none;
  pointer-events: auto;
}
.no-js .morph-button .morph-content .icon-close {
  display: none;
}
.no-js .morph-button-sidebar {
  width: 300px;
  position: fixed;
  top: 0;
  left: 0;
  margin: 0;
  height: 100%;
  background: #e85657;
  overflow: auto;
}
.no-transition {
  -webkit-transition: none !important;
  transition: none !important;
}
@media screen and (max-width: 600px) {
  .morph-button-modal.open .morph-content {
    top: 0% !important;
    left: 0% !important;
    margin: 0;
    width: 100%;
    height: 100%;
    overflow-y: scroll;
    -webkit-transition: width 0.4s 0.1s, height 0.4s 0.1s, top 0.4s 0.1s, left 0.4s 0.1s;
    transition: width 0.4s 0.1s, height 0.4s 0.1s, top 0.4s 0.1s, left 0.4s 0.1s;
  }
}
@media screen and (max-width: 400px) {
  .morph-button-fixed > button {
    font-size: 75%;
  }
  .morph-button-sidebar > button {
    font-size: 1.6em;
  }
  .morph-button-inflow .morph-content .morph-clone {
    font-size: 0.9em;
  }
  .morph-button-modal-4,
  .morph-button-modal-4 .morph-content {
    width: 220px;
    height: 120px;
  }
  .morph-button-modal-4 > button {
    font-size: 100%;
    line-height: 50px;
  }
  .morph-button-modal-4 > button span {
    display: block;
  }
  .morph-button-modal-4 .morph-clone {
    right: 83px;
    bottom: 26px;
  }
  .morph-button-sidebar,
  .morph-button-sidebar .morph-content {
    width: 100% !important;
    height: 60px !important;
  }
  .morph-button-sidebar {
    bottom: 0px;
    left: 0px;
  }
  .morph-button-sidebar.open .morph-content {
    height: 100% !important;
  }
}
.menu {
  width: 300px;
  height: 200px;
  margin: calc(100vh - 215px) auto 100px;
  position: relative;
  filter: url("../menu.html#goo");
  transform: translateZ(0);
}
.menu-wrapper {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 40px;
  height: 40px;
}
.menu-toggle-button,
.menu-item-bounce,
.menu-item-button {
  background: #fff;
  color: #000;
  border-radius: 50%;
  width: 80px;
  height: 80px;
  margin-top: -40px;
  margin-left: -40px;
  border: none;
  outline: none;
  position: relative;
}
.menu-toggle-button {
  background: transparent;
  position: relative;
  top: 0;
  left: 0;
}
.menu-toggle-icon {
  font-size: 30px;
  position: absolute;
  top: 0;
  left: 0;
  width: 80px;
  height: 80px;
  line-height: 80px;
}
.menu-items {
  list-style-type: none;
  padding: 0;
  margin: 0;
  position: absolute;
  top: 0;
  left: 0;
}
.menu-item {
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 0;
}
.menu-item-bounce {
  position: absolute;
  top: 0;
  left: 0;
}
.menu-item-button {
  width: 60px;
  height: 60px;
  margin-top: -30px;
  margin-left: -30px;
  position: absolute;
  top: 0;
  left: 0;
  color: #ad4c4c;
}
.menu-item-button:before {
  content: '';
  background: #f59393;
  width: 0;
  height: 0;
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  transition: all 1s ease-in-out;
  transform: translate(-50%, -50%);
}
.menu-item-button.is-active {
  width: 100vw;
  height: 100vh;
}
.menu-item-button:hover {
  color: #fff;
}
body {
  width: 100%;
  min-height: 150vh;
  overflow-x: hidden;
  overflow-y: scroll;
}
body {
  margin: 0;
  background-size: cover;
  background-attachment: fixed;
  font-size: 15px;
  color: #fff;
  font-family: 'Roboto', sans-serif;
  background: #222;
}
* {
  box-sizing: border-box;
}
figure {
  margin: 0;
}
.btn {
  padding: 15px;
}
/* helper class when showing content */
.noscroll {
  overflow: hidden;
}
.u-bg--violet {
  background-color: #433b8f;
}
.u-bg--orange {
  background-color: #f07502;
}
.u-bg--green {
  background-color: #7cbc19;
}
.u-bg--blue {
  background-color: #0184cd;
}
.u-bg--red {
  background-color: #f44336;
}
.u-bg--pink {
  background-color: #ff4081;
}
h1 {
  font-weight: 100;
  font-size: 45px;
  position: absolute;
  top: 40%;
  left: 0;
  right: 0;
  text-align: center;
}
h1 small {
  font-size: 0.5em;
  display: block;
}
.toggleMenu {
  background: #f07502;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  color: #fff;
  text-align: center;
  line-height: 48px;
  font-size: 25px;
  border: none;
  position: absolute;
  top: 55%;
  left: 0;
  right: 0;
  margin: auto;
}
.main {
  position: relative;
}
/* Overlay */
.grid::after {
  content: '';
  background: rgba(202,202,202,0.4);
  opacity: 0;
  position: absolute;
  pointer-events: none;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  -webkit-transition: opacity 0.5s;
  transition: opacity 0.5s;
}
.view-single .grid::after {
  opacity: 1;
}
.morph {
  width: 100%;
  transform: translate3d(0, 0, 0);
  will-change: left, top, height, width, transform;
  left: 0;
  top: 0;
  transition: all 0.3s ease;
  background: #808080;
}
#scene {
  perspective: 150px /* the lower this is, the fancier it gets */;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
#emitter {
  position: absolute;
  left: 50%;
  top: 50%;
  transform-style: preserve-3d;
}
.particle {
  position: absolute;
  width: 10px;
  height: 10px;
  background: #fff;
  border-radius: 20%;
}
